var style={
    backgroundColor:'Transparent',
    backgroundRepeat:'no-repeat',
    border:'none',
    cursor:'pointer',
    overflow:'hidden'     
}

class Menu extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state={

    data:[]
   }

}




  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }
 
  componentWillMount() {
    this.setState({
      activeTab: '1'
    });
  fetch("http://localhost:5000/getitems")
    .then(res=>res.json()).then((res)=>{
     console.log(res);
     this.setState({data:res});

    })
  }
 


  render(){
 
    return (
      <section className="menu-list-area section-gap">
      <form action="http://localhost:5000/checkout" method="post" id="menu" name="menu" encType="application/json" >
      <div className="container">
      <div className="row">  
        <div className="menu-cat mx-auto" >
  
              <ul className="nav nav-pills" id="pills-tab" role="tablist">
            
                          <li className="nav-item">
            
                            <a    className="nav-link active show" id={classnames({ active: this.state.activeTab === '1' })}
                             onClick={() => { this.toggle('1');  }} href="#1" data-toggle="pill" >Hair</a>
            
                          </li>
            
                          <li className="nav-item">
            
                            <a   className="nav-link"  id={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.toggle('2'); }} href="#2" data-toggle="pill">Skin</a>
            
                          </li>
                          <li className="nav-item">
            
            <a  className="nav-link" id={classnames({ active: this.state.activeTab === '3' })}
onClick={() => { this.toggle('3');}} href="#3" data-toggle="pill">Face</a>

                      </li>

             
          <li className="nav-item">
            
            <a   className="nav-link"  id={classnames({ active: this.state.activeTab === '4' })}
onClick={() => { this.toggle('4'); }} href="#4" data-toggle="pill" >Nails</a>

          </li>

             </ul>       
                </div>
          </div>

        <TabContent activeTab={this.state.activeTab} >
          <TabPane tabId="1">
            <Row>
              <Col sm="12">
           
              {this.state.data.filter(item=>item.itemcatogery==='hair').map((item,index)=><Item  index={index} catogery={item.itemcatogery} key={item.itemname} itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
          
              </Col>
            </Row>
          </TabPane>

          <TabPane tabId="2">
        
            <Row>
              <Col sm="12">
              {this.state.data.filter(item=>item.itemcatogery==='skin').map((item,index)=><Item  index={index} catogery={item.itemcatogery} key={item.itemname} id="skintab" itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
              
              </Col>
            </Row>

          </TabPane>
          <TabPane tabId="3">
           
          <Row>
              <Col sm="12">
              {this.state.data.filter(item=>item.itemcatogery==='face').map((item,index)=><Item index={index} catogery={item.itemcatogery} key={item.itemname} name="facetab" itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="4">
            <Row>
            <Col sm="12">
            {this.state.data.filter(item=>item.itemcatogery==='nails').map((item,index)=><Item index={index} catogery={item.itemcatogery} key={item.itemname} name="nailstab" itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
         
              </Col>
            </Row>
          </TabPane>
          <div className="d-flex flex-row-reverse">
            
            <div className="p-2"><button type="submit"style={style}><a className="genric-btn primary circle arrow" id="proceed" >Proceed<span className="lnr lnr-arrow-right"></span></a></button></div>
      </div>
        </TabContent>
 
   </div>      
        
      </form>

</section>
    );
  }
}
export default Menu;
